import uniRate from '../../components/uni-rate/uni-rate.vue';
export default {
	components: {
		uniRate
	},
	data() {
		return {
			detailObj: {},
			directors: [],
			actors: []
		};
	},
	onLoad(options) {
		this.detailObj = JSON.parse(options.item);
		uni.setNavigationBarTitle({
			title: '电影详情'
		});
		this.getDirectors();
		this.getActors();
		this.detailObj.plotPics = JSON.parse(this.detailObj.plotPics)
	},
	// 仅仅只支持小程序分享 分享到微信群或者好友
	onShareAppMessage(res) {
		return {
			title: this.detailObj.name,
			path: '/pages/detail/detail?trailerId=' + this.detailObj.id
		};
	},
	onNavigationBarButtonTap(e) {
		let index = e.index;
		if (index === 0) {
		}
	},
	// 页面初次渲染完成 获得视频对象
	onReady() {
		let video = uni.createVideoContext('movie');
	},
	// 页面隐藏时 不播放
	onHide() {
		if (this.video) {
			this.video.pause();
		}
	},
	// 页面再次显示时 播放视频
	onShow() {
		if (this.video) {
			this.video.play();
		}
	},
	methods: {
		back() {
			let pages = getCurrentPages(),
				prevPage = null;
			if (pages.length > 1) {
				prevPage = pages[pages.length - 2];
			}
			uni.navigateBack();
		},
		// 封面预览
		goCover() {
			uni.navigateTo({
				url: `/pages/cover/cover?img=` + this.detailObj.poster
			});
		},
		// 获取导演
		getDirectors() {
			uni.request({
				url: `${this.$api}/search/staff/${this.detailObj.id}/1`,
				method: 'POST',
				data: {
					qq: '7929290'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						this.directors = res.data.data;
						// this.movieList = res.data.data.rows;
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		// 获取演员
		getActors() {
			uni.request({
				url: `${this.$api}/search/staff/${this.detailObj.id}/2`,
				method: 'POST',
				data: {
					qq: '7929290'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					if (res) {
						this.actors = res.data.data;
						// this.movieList = res.data.data.rows;
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		}
	}
};
